import React from 'react';
const CoverageA = ()=>(
    <div>
        <div className="card__title">
                <h5 className="bold-text"> </h5>
        </div>
        <ul className="list list--icon">"We" insure:
                <li><p><span className="lnr lnr-checkmark-circle" />the "dwelling" on the "premises" described on the Declarations and its attached structures;</p></li>
                <li><p><span className="lnr lnr-checkmark-circle" />permanently installed outdoor equipment on the "premises" used principally for the service of the "dwelling";</p></li>
                <li><p><span className="lnr lnr-checkmark-circle" />outdoor "domestic water containers", including swimming pool(s), spas, hot tubs and their
                        attached equipment on the "premises";</p></li>
                <li><p><span className="lnr lnr-checkmark-circle" />materials and supplies located on or adjacent to the "premises" intended for use in construction,
                        alteration or repair of the "dwelling" and/or private structures on the "premises";</p></li>
                <li><p><span className="lnr lnr-checkmark-circle" />building fixtures and fittings used principally for the service of the "dwelling" while temporarily
                        removed from the "premises" for repair or seasonal storage.</p></li>
        </ul>
    </div>
);
export default CoverageA;