import React from 'react';
import Collapse from '../../../shared/components/Collapse';
const CoverageD = ()=>(
    <div>
        The Amount of Insurance for COVERAGE D  LOSS OF USE is the total amount for any one or a
        combination of the following coverages. The periods of time shown below are not limited by the expiration
        of this policy.
        <Collapse title="Additional living expense" className="with-shadow">
            As a result of an insured peril, if "your" "dwelling" is unfit for occupancy or "you" have to move out
            while repairs are being made, "we" insure any necessary increase in living expenses, including
            moving expenses, incurred by "you" so that "your" household can maintain its normal standard of
            living. Payment shall be for the reasonable time required to repair or rebuild "your" "dwelling", or if
            "you" permanently relocate, the reasonable time required for "your" household to settle elsewhere.
        </Collapse>
        <Collapse title="Fair rental value" className="with-shadow">
            If an insured peril makes that part of the "dwelling" and/or private structure rented to others or held
            for rental by "you" unfit for occupancy, we insure its Fair Rental Value.
            Payment shall be for the reasonable time required to repair or replace that part of the "dwelling"
            and/or private structure rented or held for rental.
            Fair Rental Value shall not include any expense that does not continue while that part of the
            "dwelling" and/or private structure rented or held for rental is unfit for occupancy.
        </Collapse>
        <Collapse title="Prohibited access" className="with-shadow">
        <ul className="list list--icon list--icon-green">If a "civil authority" prohibits access to "your" "premises":
            <li><p><span className="lnr lnr-checkmark-circle" /> as a direct result of damage to neighbouring "premises" by an insured peril in this policy, "we"
            insure any resulting Additional Living Expense and Fair Rental Value loss for a period not
            exceeding two weeks; or</p></li>
            <li><p><span className="lnr lnr-checkmark-circle" />by order for mass evacuation as a direct result of a sudden and accidental event within Canada
            or the United States of America, "we" insure any resulting necessary and reasonable increase
            in living expense incurred by "you" for the period access is prohibited, not exceeding two weeks.
            "You" are not insured for any claim arising from evacuation resulting from:</p></li>
        </ul>
            {/* (1) losses excluded under SECTION I  LOSS OR DAMAGE NOT INSURED;
            (2) "flood";
            (3) earthquake, unless EARTHQUAKE COVERAGE  FORM 991 or CONDOMINIUM UNIT
            OWNERS EARTHQUAKE COVERAGE  FORM 991C is shown on the Declarations;
            (4) overland water, unless OVERLAND WATER COVERAGE  FORM 3115 or 3115L is
            shown on the Declarations.
            No deductible applies to Prohibited Access coverage.
            "We" do not insure loss or expense due to the cancellation of a lease or agreement. */}
        </Collapse>

    </div>
);
export default CoverageD;