import React, {Component} from 'react';
import Slider from '../../../../shared/components/range_slider/Slider';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import {changeValue5} from '../../../../redux/actions/rangeSliderAction';
class Slider5  extends Component{
    static propTypes = {
        dispatch: PropTypes.func.isRequired,
        priceValue5: PropTypes.number.isRequired
      };
      changeValue5 = (priceValue5) => {
        const {dispatch} = this.props;
        dispatch(changeValue5(priceValue5));
      }
    render(){
        return(
            <Slider
            min={0}
            max={100}
            marks={{
            0: '0',
            30: '30,000',
            50: '50,000',
            70: '70,000',
            100: '100,000',
            }}
            onChange = {this.changeValue5}
            />
        );
    }
}
export default withRouter(connect(state => ({
    priceValue5: state.priceValue5,
  }))(Slider5));