import React from 'react';
import Collapse from '../../../shared/components/Collapse';
const CoverageC = ()=>(
    <div>
        <Collapse title="Personal property on your premises" className="with-shadow">
            <ul className="list list--icon list--icon-green">"We" insure:
                <li><p><span className="lnr lnr-checkmark-circle"/>the contents of "your" "dwelling" and other personal property "you" own, wear or use which is
                usual to the ownership, maintenance or use of "your" "dwelling";</p></li>
                <li><p><span className="lnr lnr-checkmark-circle"/>if "you" wish "we" will include uninsured personal property of others while it is on that portion of
                "your" "premises" which "you" occupy, but we do not insure personal property of roomers or
                boarders.</p></li>
            </ul>
        </Collapse>
        <Collapse title="Personal property away from your premises" className="with-shadow">
        <ul className="list list--icon list--icon-green">We" insure:
            <li><p><span className="lnr lnr-checkmark-circle"/>"your" personal property while it is temporarily away from "your" "premises" anywhere in the
            world. If "you" wish "we" will include uninsured personal property belonging to others while it is
            in "your" possession or belonging to a "residence employee" travelling with "you";</p></li>
            <li><p><span className="lnr lnr-checkmark-circle"/>personal property while in storage, including in a safety deposit box;</p></li>
            <li><p><span className="lnr lnr-checkmark-circle"/>personal property of a student who, while temporarily residing away from "your" principal
            residence is covered up to $25,000 provided the student:
                <p>(1) is enrolled in and attending a school, college or university; and</p>
                <p>(2) is dependent on a "Named Insured" or his or her "spouse" for support and maintenance;</p>
            </p></li>
            <li><p><span className="lnr lnr-checkmark-circle"/>personal property of an "Insured" or parent of an "Insured" who is dependent on the "Named
            Insured" or his or her "spouse" for support and maintenance while residing in a nursing home
            or assisted living facility is covered up to $25,000.</p></li>
        </ul>
        </Collapse>
        <Collapse title="Moving to another home" className="with-shadow">
            "We" insure "your" personal property while "you" are moving from the "premises" shown on the
            Declarations to a new "dwelling" anywhere in Canada:
            <ul className="list list--icon list--icon-green">
                <li><p><span className="lnr lnr-checkmark-circle"/>for a period of 30 days commencing on the date "you" began moving; or</p></li>
                <li><p><span className="lnr lnr-checkmark-circle"/>until "your" policy term expires or is terminated;
                whichever occurs first.</p></li>
                This coverage does not increase the Amount of Insurance applying to the property being moved.
            </ul>
        </Collapse>
        <Collapse title="Special limits of insuranceving to another home" className="with-shadow">
        </Collapse>
        <Collapse title="Property not insured" className="with-shadow">
        "We" do not insure:
        <ul className="list list--icon list--icon-green">
            <li><p><span className="lnr lnr-checkmark-circle"/>property of roomers or boarders who are not related to "you";</p></li>
            <li><p><span className="lnr lnr-checkmark-circle"/>personal property normally kept at any other location "you" own, rent or occupy;</p></li>
            <li><p><span className="lnr lnr-checkmark-circle"/>motorized vehicles or their equipment except:
                <p>electric power assisted bicycles;</p>
                <p>wheelchairs or scooters having more than two wheels and specifically designed for the
                    carriage of a person who has a physical disability, that can attain speeds no greater than
                    32 kilometers per hour;</p>
                <p>watercraft;</p>
                <p>lawn mowers, snow blowers and other motorized gardening equipment; and</p>
                <p>motorized golf carts;</p>
            </p></li>
            <li><p><span className="lnr lnr-checkmark-circle"/>camper units, truck caps, trailers, or their equipment;</p></li>
            <li><p><span className="lnr lnr-checkmark-circle"/>"aircraft" or their equipment.</p></li>
            <li><p><span className="lnr lnr-checkmark-circle"/>sporting equipment where the loss or damage is due to its use;</p></li>
            <li><p><span className="lnr lnr-checkmark-circle"/>breakage of eyeglasses, glassware, statuary, marbles, bric-a-brac, porcelain, and similar fragile
            articles (other than jewellery, gems, watches, bronzes, precious and semi-precious stones,
            cameras and photographic lenses) unless the loss or damage is caused by;</p></li>
            <li><p><span className="lnr lnr-checkmark-circle"/></p>animals, birds or fish unless the loss or damage is caused by any of the "Specified Perils" other
            than IMPACT BY AIRCRAFT, SPACECRAFT OR LAND VEHICLE;</li>
            <li><p><span className="lnr lnr-checkmark-circle"/></p>property at any fairground, exhibition or exposition, for the purpose of exhibition, trade or sale;</li>
            <li><p><span className="lnr lnr-checkmark-circle"/></p>evidence of debt or title; or</li>
            <li><p><span className="lnr lnr-checkmark-circle"/></p>property pertaining to a business, including samples and goods held for sale.</li>
        </ul>
        {/* aircraft:
            Equipment includes audio, visual, recording, or transmitting equipment, powered by the electrical
            system of a motorized vehicle or "aircraft".
            Equipment does not include spare automobile parts.
            Toys or hobby items such as model "aircraft" or children's battery powered all-terrain vehicles using
            no more than a 12 volt battery or that can attain speeds no greater than 10 kilometers per hour are
            not considered motorized vehicles or "aircraft";
            f. 
            g. breakage
            (1) any of the "Specified Perils"; or
            (2) theft or attempted theft; */}
        </Collapse>
    </div>
);
export default CoverageC;