import React from 'react';
import Collapse from '../../../shared/components/Collapse';
const CoverageE = ()=>(
    <div>
        <ul className="list list--icon list--icon-green"><p>"We" will pay all sums which "you" become legally liable to pay as compensatory damages because of
            unintentional "bodily injury" or "property damage" arising out of:</p>
            <li><p><span className="lnr lnr-checkmark-circle"/>"your" personal actions anywhere in the world;</p></li>
            <li><p><span className="lnr lnr-checkmark-circle"/>"your" ownership, use or occupancy of the "premises".</p></li>
        </ul>
        <ul className="list list--icon list--icon-green"><p>The Amount of Insurance shown on the Declarations is the maximum amount "we" will pay for all
            compensatory damages in respect to one accident or "occurrence" regardless of the number of "Insureds"
            against whom claims are made or actions are brought.</p>
            <p>Defense, costs and supplementary expense payments as described under DEFENSE, SETTLEMENT
            and SUPPLEMENTARY PAYMENTS are in addition to the Amount of Insurance.</p>
            <p>"We" do not insure claims made against "you" arising from:</p>
            <li><p><span className="lnr lnr-checkmark-circle"/>liability "you" have assumed by contract unless "your" "legal liability" would have applied even
            if no contract had been in force, but we do insure claims made against "you" for the "legal
            liability" of other persons in relation to "your" "premises" that "you" have assumed under a
            written contract;</p></li>
            <li><p><span className="lnr lnr-checkmark-circle"/> damage to property owned by an "Insured";</p></li>
            <li><p><span className="lnr lnr-checkmark-circle"/>damage to property used, occupied, leased or rented by or in the care, custody or control of an
            "Insured", except for unintentional "property damage" to "premises" owned by others, or their
            contents, which "you" are using, renting or have in "your" custody or control caused by fire,
            explosion, water damage or smoke as described in "Specified Perils";</p></li>
            <li><p><span className="lnr lnr-checkmark-circle"/>damage to personal property or fixtures as a result of work done on them by "you" or anyone
            on "your" behalf;</p></li>
            <li><p><span className="lnr lnr-checkmark-circle"/>"bodily injury" to "you" or any person residing in "your" household other than a "residence
            employee";</p></li>
            <li><p><span className="lnr lnr-checkmark-circle"/>the personal actions of a "Named Insured" who does not reside on the "premises" described
            on the Declarations.</p></li>
            <p>There are other exclusions that apply to all Coverages under Section II, Please refer to SECTION II 
            EXCLUSIONS.</p>
        </ul>
        <Collapse title="Defense, settlement, supplementary payments" className="with-shadow">
        <ul className="list list--icon list--icon-green">
        <p>If a claim is made against "you" for which "you" are insured under COVERAGE E, "we" will defend "you",
            even if the claim is groundless, false, or fraudulent. "We" reserve the right to select legal counsel,
            investigate, negotiate and settle any claim if "we" decide this is appropriate. "We" will pay only for the legal
            counsel "we" select.
            "We" will also pay:</p>
            <li><p><span className="lnr lnr-checkmark-circle"/>all expenses which "we" incur;</p></li>
            <li><p><span className="lnr lnr-checkmark-circle"/>all costs charged against "you" in any suit insured under COVERAGE E;</p></li>
            <li><p><span className="lnr lnr-checkmark-circle"/>any interest accruing after judgment on that part of the judgment which is within the Amount of
            Insurance of COVERAGE E;</p></li>
            <li><p><span className="lnr lnr-checkmark-circle"/> premiums for appeal bonds required in any lawsuit involving "you" and bonds to release any
            property that is being held as security, up to the Amount of Insurance, but "we" are not obligated
            to apply for or provide these bonds;</p></li>
            <li><p><span className="lnr lnr-checkmark-circle"/>expenses which "you" have incurred for emergency medical or surgical treatment to others
            following an accident or "occurrence" insured by this form; and/or</p></li>
            <li><p><span className="lnr lnr-checkmark-circle"/>reasonable expenses including actual loss of income up to $100 per day, which "you" incur at
            "our" request.</p></li>
        </ul>
        </Collapse>
        <Collapse title="Unauthorized settlements" className="with-shadow">
            "You" shall not, except at "your" cost, voluntarily make any payment, assume any obligations or incur
            expenses, other than first aid expenses necessary at the time of the accident or "occurrence".
        </Collapse>
    </div>
);
export default CoverageE;



