import React from 'react';
const CoverageF = ()=>(
    <div>
        <p>"We" will pay reasonable medical expenses incurred within one year of the date of the accident or
        "occurrence", if "you" unintentionally injure another person or if they are accidentally injured on "your"
        "premises". This coverage is available even though "you" are not legally liable.
        Medical expenses include surgical, dental, hospital, nursing, ambulance service and funeral expenses.
        The Amount of Insurance shown on the Declarations is the most "we" will pay for each person in respect
        to one accident or "occurrence". Payment under COVERAGE F is not an admission of liability by "you" or
        "us".</p>
        <ul className="list list--icon list--icon-green">"We" will not pay for:
            <li><p><span className="lnr lnr-checkmark-circle"/>expenses covered by any medical, dental, surgical or hospitalization plan or law, or under any
        other insurance contract;</p></li>
            <li><p><span className="lnr lnr-checkmark-circle"/>"your" medical expenses or those of persons residing with "you", other than "residence
        employees";</p></li>
            <li><p><span className="lnr lnr-checkmark-circle"/>medical expenses of any person covered by any Workers' Compensation Statute.</p></li>
        </ul>
        <p> There are other exclusions that apply to all coverages under Section II. Please refer to SECTION II 
        EXCLUSIONS.</p>
    </div>
);
export default CoverageF;