import React from 'react';
const CoverageB = ()=>(
    <div>
        <p>"We" insure private structures on the "premises" separated from the "dwelling" by a clear space but not
            insured under COVERAGE A. If they are connected to the "dwelling" by a fence, utility line or similar
            connection only, they are considered to be detached private structures.</p>
        <p>"We" do not insure private structures originally built and previously used for "farming" purposes regardless
            of their current use unless otherwise endorsed in this form.
        </p>
    </div>
);
export default CoverageB;